<template>
    <div class="bg">
        <div class="user-login" :class="[{'regiest': !status}, 'user-login']">
            <strong class="name">{{status ? '   ' : '   '}}</strong>
            <div class="user-info">
                <span class="info-name">  </span>
                <span class="info-input">
                    <el-input v-model="form.user_name" placeholder=""></el-input>
                </span>
            </div>
            <div class="user-info">
                <span class="info-name"> </span>
                <span class="info-input">
                    <el-input @keyup.enter.native="login()" placeholder="" v-model="form.password" show-password></el-input>
                </span>
            </div>
            <div class="user-info" v-if="!status">
                <span class="info-name"></span>
                <span class="info-input">
                    <el-input placeholder="" v-model="form.passwordAgain" show-password></el-input>
                </span>
            </div>
            <div class="handle-btn">
                <template v-if="status">
                    <el-button type="primary" @click="login()"> </el-button>
                    <el-button type="danger" @click="status = false"> </el-button>
                </template>
                <template v-else>
                    <el-button type="danger" @click="reg()"> </el-button>
                    <span class="diff-btn" @click="status = true"></span>
                </template>
            </div>
        </div>
    </div>
</template>
<script>
import { userRegiest, login } from '@/service/service.js'
export default {
    props: [],
    data() {
        return {
            form: {
                user_name: '',
                password: '',
                passwordAgain: ''
            },
            status: true
        }
    },
    methods: {
        async reg () {
            if (this.form.user_name === '') {
                this.$message.error('')
                return
            } else if (this.form.password === '') {
                this.$message.error('')
                return
            } else if (this.form.password !== this.form.passwordAgain) {
                this.$message.error('')
                return
            }
            let param = {
                user_name: this.form.user_name,
                password: this.form.password
            }
            const res = await userRegiest(param)
            if (res.code === 0) {
                this.$message.success('')
                this.status = true
                Object.keys(this.form).forEach(p => {
                    this.$set(this.form, p, '')
                })
            } else {
                this.$message.success('')
            }
        },
        async login () {
            if (!this.status) return
            let param = {
                user_name: this.form.user_name,
                password: this.form.password
            }
            const res = await login(param)
            if (res.code === 0) {
                sessionStorage['info'] = res.data.id
                this.$router.replace({path: '/home'})
            } else {
                this.$message.error(res.msg)
                return
            }
        }
    },
}
</script>
<style lang="scss" scoped>
    .bg {
        width: 100%;
        height: 100%;
        background: url('/static/img/back.png') no-repeat center;
        position: relative;
        .user-login{
            width: 40rem;
            position: absolute;
            border: 1px solid #ccc;
            top: 30%;
            right: 10%;
            border-radius: 5px;
            display: flex;
            flex-direction: column;
            background:linear-gradient(90deg,#f1f3ff 0%, #576b73);
            height: 30rem;
        }
        .regiest {
            height: 35rem;
        }
        .name {
            font-size: 2.4rem;
            width: 100%;
            height: 7rem;
            line-height: 7rem;
            text-align: center;
            letter-spacing: 5px;
        }
        .user-info {
            width: 100%;
            height: 7rem;;
            padding: 0 2rem;
            display: flex;
            justify-content: flex-start;
            .info-name {
                width: 10rem;
                font-size: 1.6rem;
                height: 100%;
                line-height: 2.5;
                letter-spacing: 3px;
            }
            .info-input {
                flex: 1;
            }
        }
        .handle-btn {
            flex: 1;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            .diff-btn {
                cursor: pointer;
                margin: 0 2rem;
                font-size: 1.4rem;
                border-bottom: 1px solid #000;
                padding: 3px 5px;
            }
        }
    }
</style>


